import React, { Component, createRef } from 'react';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import IconButton from '@material-ui/core/IconButton';
import InputBase from '@material-ui/core/InputBase';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Avatar from '@material-ui/core/Avatar';
import Typography from '@material-ui/core/Typography';
import SearchIcon from '@material-ui/icons/Search';
import CircularProgress from '@material-ui/core/CircularProgress';
import { fromEvent } from 'rxjs';
import { map, mergeMap, debounceTime, filter, distinctUntilChanged } from 'rxjs/operators';
import { ajax } from 'rxjs/ajax';

const styles = (theme) => ({
  root: {
    ...theme.mixins.gutters(),
    padding: theme.spacing.unit * 2,
    textAlign: 'center',
    width: 400,
    margin: '16px auto 0 auto',
    color: theme.palette.text.secondary,
  },
  search: {
    textAlign: 'center',
    display: 'flex',
    alignItems: 'center',
  },
  input: {
    marginLeft: 8,
    flex: 1,
  },
  iconButton: {
    padding: 10,
  },
  divider: {
    width: 1,
    height: 28,
    margin: 4,
  },
  list: {
    width: '100%',
    maxWidth: 360,
    backgroundColor: theme.palette.background.paper,
  },
  inline: {
    display: 'inline',
  },
  progress: {
    margin: theme.spacing.unit * 2,
  },
});

class Index extends Component<any> {
  searchRef = createRef();
  user$;
  state = {
    userList: []
  };

  componentDidMount() {
    console.log(this.searchRef.current);
    this.user$ = fromEvent(this.searchRef.current as any, 'keyup').pipe(
      debounceTime(300),
      map((event: React.ChangeEvent<HTMLInputElement>) => event.target.value),
      distinctUntilChanged(),
      filter((query: string) => query.trim().length > 0),
      mergeMap((query: string) => ajax.getJSON(`https://api.github.com/search/users?q=${query}`)),
    );
    console.log(this.user$);
    this.user$.subscribe(v => {
      this.setState({
        userList: this.drawLayer(v.items)
      })
    //   this.test = this.drawLayer(v.items);
    })
  }

  componentWillUnmount() {
    this.user$.unsubscribe();
  }

  drawLayer = (items) => {
    const classes = styles(this.props.pageContext.theme);

    return items.map(user => (
      <ListItem alignItems='flex-start'>
        <ListItemAvatar>
          <Avatar alt='Remy Sharp' src={user.avatar_url} />
        </ListItemAvatar>
        <ListItemText
          primary='Oui Oui'
          secondary={
            <Typography component='span' style={classes.inline} color='textPrimary'>
              {user.login}
            </Typography>
          }
        />
      </ListItem>
    ));
  };

  render() {
    const classes = styles(this.props.pageContext.theme);

    return (
      <Paper style={classes.root} elevation={1}>
        <Grid container>
          <Grid item xs={12} style={classes.search}>
            <InputBase
              inputRef={this.searchRef}
              style={classes.input}
              placeholder='Search Github User Profile'
            />
            <IconButton style={classes.iconButton} aria-label='Search'>
              <SearchIcon />
            </IconButton>
          </Grid>
          {this.state.userList.length > 0 ?
            (
              <Grid item xs={12}>
                <List style={classes.list}>
                  {this.state.userList}
                </List>
              </Grid>
            ) : (
              <CircularProgress/>
            )
          }
        </Grid>
      </Paper>
    );
  }
}

export default Index;